export default {
	data() {
		return {
			n:0,
			status:0,
			tabList:['待审核','已同意','已拒绝'],
			list:[]
		}
	},
	onShow() {
		// this.status = 0
		this.getData()
	},
	methods: {
		ClubAudit(id){
			uni.navigateTo({
				url:'../../pagesThree/ClubAudit/ClubAudit?id='+id
			})
		},
		getData(){
			this.$api.check_club_apply_list({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				status:this.status
			}).then(res=>{
				if (res.data.code === '200') {
				    this.list = res.data.data
				} else if (res.data.code === '1092') {
					uni.showToast({
					    title: '暂无权限',
					    mask: "true",
						icon:'none'
					});
				}
				
			})
		},
		tab(index){
			this.n = index
			this.status = index
			this.getData()
		}
	}
}
